import React, { useState, useEffect } from 'react';
import './index.scss';
import { widget as WidgetBase } from '../charting_library/charting_library/charting_library.min';

function getLanguageFromURL() {
	const regex = new RegExp('[\\?&]lang=([^&#]*)');
	const results = regex.exec(window.location.search);
	return results === null ? null : decodeURIComponent(results[1].replace(/\+/g, ' '));
}
export const TVChartContainer = ({
	symbol='AAPL',
	interval='1',
	containerId='tv_chart_container',
	datafeedUrl,//='https://demo_feed.tradingview.com',
	libraryPath='/charting_library/',
	chartsStorageUrl=null,//'https://saveload.tradingview.com',
	chartsStorageApiVersion=null,//'1.1',
	clientId='tradingview.com',
	userId='public_user_id',
	fullscreen=false,
	autosize=true,
	studiesOverrides={},
	datafeed,
	// eslint-disable-next-line no-unused-vars
	onChartReady=({widget, options})=>{}
}) => {
	const [tvWidget, setWidget] = useState(null);
	useEffect(() => {
		const datafeedResult =
			typeof datafeed!=="undefined" ? datafeed :
			typeof datafeedUrl==="string" && datafeedUrl.length>0 ? new window.Datafeeds.UDFCompatibleDatafeed(datafeedUrl) :
			null;
		if(datafeedResult===null) {
			console.error("datafeed and datafeedUrl missing")
		}
		const options = {
			symbol,
			datafeed: datafeedResult,
			interval,
			container_id: containerId,
			library_path: libraryPath,
			locale: getLanguageFromURL() || 'en',
			disabled_features: ['use_localstorage_for_settings'],
			enabled_features: ['study_templates'],
			charts_storage_url: chartsStorageUrl,
			charts_storage_api_version: chartsStorageApiVersion,
			client_id: clientId,
			user_id: userId,
			fullscreen,
			autosize,
			studies_overrides: studiesOverrides,
		};

		const widget = new WidgetBase(options);
		console.log({widget: widget._iFrame, WidgetBase});
		widget.onChartReady(() => {
			console.log("onChartReady");
			onChartReady({widget, options});
		});
		widget.headerReady(() => {
			console.log("headerReady");
		})
		setWidget(widget);
		return () => {
			if (tvWidget !== null) {
				tvWidget.remove();
				setWidget(null);
			}
		}
	}, []);

	return <div id={containerId} className={'TVChartContainer'}/>
}