import React from "react";
import { route } from "navi"
import { Button, Input } from "components";
import { Link } from "react-navi";
import "../auth-account-style.scss";
import { HiDaraImage } from "../components/HiDaraImage";
export const ForgotPassword = () => {
	return <div className="auth-account-style">
		<HiDaraImage />
		<div className="auth-content">
			<h1>{`Forgot Password?`}</h1>
			<p>{`Enter your email below to receive password reset link to your inbox`}</p>
			<Input placeholder="Email Address"></Input>
			<Button>
				Reset password
			</Button>
			<p>{`Don't have an account? `}<Link href="/createnewaccount">{`Sign Up... It's free`}</Link></p>
		</div>
	</div>
}

export default route({
	title: "Forgot Password",
	getData: () => ({testing: 42}),
	view: <ForgotPassword />,
});